@use '../util/' as *;

.cards{

    display: grid;
    grid-template-columns: 1fr;
    gap: rem(30);

    @include breakpoint(medium){
        grid-template-columns: repeat(2, 1fr);
    }
    
    @include breakpoint(large){
        grid-template-columns: repeat(4, 1fr);
    }
}

// elements with class of card
.card {
    position: relative;
    overflow: hidden;
    background: var(--dark-card);
    color: var(--dark-text1);
    width: rem(255);
    text-align: center;
    padding: rem(25);
    border-radius: rem(5);

    &--facebook{
        border-top: rem(5) solid var(--facebook);
    }

    &--twitter{
        border-top: rem(5) solid var(--twitter);
    }

    &--instagram{
        padding-top: rem(30);
        &::before{
            content: '';
            height: rem(5);
            width: 100%;
            background: linear-gradient(225deg, var(--instagram-end) 0%, var(--instagram-middle) 50.91%, var(--instagram-start) 100%);
            display: block;
            position: absolute;
            left: 0;
            top: 0;
        }
        
    }

    &--youtube{
        border-top: rem(5) solid var(--youtube);
    }

    &__platform{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: rem(5);
        margin-bottom: rem(28);
        height: rem(20);
    }

    &__icon{

        margin-right: rem(8);
        

        &--facebook{
            
        }

        &--twitter{

        }

        &--instagram{

        }

        &--youtube{

        }

    }

    &__username{
        font-size: rem(12);
        font-weight: bold;
    }

    &__followers{
        margin-bottom: rem(25);
    }

    &__count{

        color: var(--light-bg);
        font-weight: bold;
        letter-spacing: rem(-2);
        line-height: 1;
        margin-bottom: rem(4);

        &--big{
            font-size: rem(56);
        }

        &--small{

        }
    }

    &__label{
        font-size: rem(12);
        letter-spacing: rem(5);
        font-weight: 400;
        color: var(--dark-text1);
        text-transform: uppercase;
    }

    &__change{
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: rem(12);
        font-weight: 700;

        &--up{
            color: var(--limeGreen);
        }

        &--down{
            color: var(--brightRed);
        }
    }

    &__number{
        margin-left: rem(4);
    }
}